@import url("https://fonts.googleapis.com/css?family=Monoton|Montserrat");
@-webkit-keyframes AnimationName {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 200% 50%; } }
@keyframes AnimationName {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 200% 50%; } }

header {
  background: -webkit-gradient(linear, right top, left top, from(#23A6D5), color-stop(#4A148C), color-stop(#FF0066), color-stop(#4A148C), to(#23A6D5));
  background: -webkit-linear-gradient(right, #23A6D5, #4A148C, #FF0066, #4A148C, #23A6D5);
  background: -o-linear-gradient(right, #23A6D5, #4A148C, #FF0066, #4A148C, #23A6D5);
  background: linear-gradient(to left, #23A6D5, #4A148C, #FF0066, #4A148C, #23A6D5);
  background-size: 400% 400%;
  -webkit-animation: AnimationName 60s ease infinite;
          animation: AnimationName 60s ease infinite;
  width: 100%;
  margin: 0 auto 30px;
  padding: 60px 0; }
  header h1 {
    font-size: 5rem;
    letter-spacing: normal;
    color: #ffffff;
    margin: 0 auto; }
  header h3 {
    font-weight: 400;
    color: #ffffff;
    margin-bottom: 30px; }

@media screen and (max-width: 600px) {
  header h1 {
    font-size: 3rem; }
  header h3 {
    font-size: 1.8rem; } }



