@import url("https://fonts.googleapis.com/css?family=Monoton|Montserrat");
nav.navbar {
  min-height: 50px;
  border-radius: 0;
  border: 1px solid transparent; }
  nav.navbar .navbar-brand {
    font-size: 5rem;
    font-family: "Monoton", "Montserrat", "Source Sans Pro", "Helvetica", "Arial", sans-serif;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s; }
  nav.navbar li {
    display: inline-block; }

.navbar-default {
  background-color: #ffffff; }

nav.navbar.navbar-fixed-top {
  height: 35px; }
  nav.navbar.navbar-fixed-top .navbar-brand {
    font-size: 2.5rem;
    float: left; }

@media screen and (max-width: 767px) {
  .container .navbar-header {
    margin-top: 1.2em; }
  nav.navbar .navbar-brand {
    font-size: 2em;
    float: none; }
  nav.navbar.navbar-fixed-top .container .navbar-header {
    float: left;
    display: inline-block; }
  nav.navbar.navbar-fixed-top .navbar-brand {
    font-size: 2.5rem;
    float: left;
    padding: 0 15px; }
  nav.navbar.navbar-fixed-top .navbar-nav {
    float: right; } }

@media screen and (max-width: 600px) {
  nav.navbar .navbar-brand {
    font-size: 1em; } }



